.form99 {text-align: left;}

a {
	color: #3ad4af;
}

.area
{

width:100%;
min-height: 100vh;
color: white;

}
.area ul li{
	list-style: none;

}
.side-menu{
	position:relative;
	background: #275b85;
	min-height: 30rem;
	width: 300px;
	display: flex;
	border-radius: 20px;
	flex-direction: column;
}
.side-menu .brand-name{
	height: 12vh;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color:#3ad4af;
	
}

.side-menu li{
	font-size: 24px;
	padding: 17px 0px;
	color: white;
	display: flex;
	align-items: center;
	

}
.side-menu li a{
	color: #ffff;
	text-decoration: none;
}
.area li:hover{
background: rgb(97, 145, 184);
  border-radius: 10px;
  font-weight: 550;
  margin-left: 5px;
  transition: all ease-in 250ms;
}
.content1{
	
	margin-top: -760px;
	margin-left: 300px;
	min-height: 90vh;
	display: flex;
	
	
}
.content1 .cards77{
	padding: 10px 5px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	border-radius: 30px;
}
.content1 .cards77 .card00{
	width: 150px;
	height: 100px;
	background-color: rgb(35, 44, 75);
	margin: 10px 5px;
	border-radius: 20px;
	display: flex;
	align-items: center;
	justify-content: space-around;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, .16),0 6px 20px 0 rgba(240, 229, 229, 0.19) ;
}
.box00 h1, h3{
	font-size: 16px;
	color: #fff;
	letter-spacing: 1px;
	text-align: center;

}


